	module.exports = {"data":{"site":{"siteMetadata":{"title":"slncu.com","author":"Shunsuke Urita"}},"markdownRemark":{"id":"/Users/sinc/Desktop/workspace/slncu/src/pages/posts/20180125/index.md absPath of file >>> MarkdownRemark","html":"<p>gatsby.jsを使ってブログを開設しました。\nひとまずロゴだけ作って公式のサンプルからほとんど変えていません。\nstyled-componentsに慣れたせいかglamorみたいなCSSの書き方にあまり慣れません…。\nNext.js,ReactStaticと一通り触りましたが何だかんだgatsbyが使いやすいし、何より早い気がします。\nしばらくgatsbyを使い倒したいと思います。</p>\n<h2>ひとまず新年の抱負</h2>\n<p>昨年は主にフロントエンドの技術面で学びを得ることが多かったです。\n今年はバックエンド(今のところRuby on Rails5)とデザインの方面にも手を出していきたいなと思っています。\nあとはDB周りの処理とかAPIのI/F設計とか、業務でそれなりに知識必要にも関わらず浅いので頑張ります。<br></p>\n<p>とりあえず今日は遅いのでここまで。</p>","frontmatter":{"title":"開設しました","date":"January 25, 2018"}}},"pathContext":{"slug":"/posts/20180125/","previous":{"fields":{"slug":"/posts/20150528/"},"frontmatter":{"title":"New Beginnings"}},"next":false}}

